* {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
}

body {
  width: 100%;
  height: 100vh;
  background-image: url(/Lottery/static/media/debut_light.b70048d2.png);
  overflow: hidden;
}
