  #root {
    display:flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
.wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(160deg, yellow, goldenrod);
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 30px black;
  overflow: hidden;
}

.star {
  margin: 50px 0;
  position: absolute;
  z-index: -1;
  display: block;
  color: red;
  width: 0px;
  height: 0px;
  border-right: 100px solid transparent;
  border-bottom: 70px solid yellow;
  border-left: 100px solid transparent;
  right: 0;
  top:0;
  -webkit-transform: rotate(0deg) translate(100%, 100%);
          transform: rotate(0deg) translate(100%, 100%);
  -webkit-animation: starMove 15s linear infinite;
          animation: starMove 15s linear infinite;
}
.star:before {
  border-bottom: 80px solid yellow;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  position: absolute;
  height: 0;
  width: 0;
  top: -45px;
  left: -65px;
  display: block;
  content: '';
  -webkit-transform: rotate(-35deg);
          transform: rotate(-35deg);
}
.star:after {
  position: absolute;
  display: block;
  color: yellow;
  top: 3px;
  left: -105px;
  width: 0px;
  height: 0px;
  border-right: 100px solid transparent;
  border-bottom: 70px solid yellow;
  border-left: 100px solid transparent;
  -webkit-transform: rotate(-70deg);
          transform: rotate(-70deg);
  content: '';
}

@-webkit-keyframes starMove {
  100% {
    -webkit-transform: rotate(360deg) translate(100%, 100%);
            transform: rotate(360deg) translate(100%, 100%);
  }
}

@keyframes starMove {
  100% {
    -webkit-transform: rotate(360deg) translate(100%, 100%);
            transform: rotate(360deg) translate(100%, 100%);
  }
}

@media (min-width: 1200px) {
.wrapper {
  width: 75%;
  height: 75vh;
  border-radius: 1vw;
  overflow: visible;
  overflow: initial;
}
}

@media (min-height: 1200px) and (orientation: portrait) {
  .wrapper {
    width: 75%;
    height: 75vh; 
    border-radius: 1vw;
    overflow: visible;
    overflow: initial;
  }
}
header {
    flex-basis: 15vh;
    background-image: linear-gradient( #0079b2, #009be4 20%);
    text-align: center;
    padding: 2vh;
}

h1 {
    font-size: 6vw;
    line-height: 15vh;
    letter-spacing: 2px;
    font-weight: 700;
}

@media (orientation: portrait) { 
    header {
        flex-basis: 10vh;
    }  
    h1 {
        font-size: 10vw;
        line-height: 10vh;
    }
}

@media (min-width:810px) {
    h1 {
        font-size: 4.5vw;
    }
}

@media (min-width:1200px) {
    header {
        border-top-right-radius: 1vw; 
        border-top-left-radius: 1vw; 
    }
    h1 {
        font-size: 3vw;
    }
}

@media (min-height: 1200px) and (orientation: portrait) {
    header {
        border-top-right-radius: 1vw; 
        border-top-left-radius: 1vw; 
    }
}
.buttonWrapper {
    flex-basis: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid black;
}

button {
    font-size: 3vw;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 1vw 5vw;
    background-color: transparent;
    border: 2px solid #000;
    border-radius: 1vw;
    cursor: pointer;
    outline:none;
    transition: .3s;
}

button:hover {
    background-color: #009be4;
    border: 2px solid #009be4;
    color: #fff;
}

@media (orientation: portrait) {
    .buttonWrapper {
        flex-basis: 20vh;
    }
    
    button {
        font-size: 6vw;
        padding: 2vw 10vw;
    }
}

@media (min-width:1200px) {
    button {
        font-size: 2vw;
    }
}

@media (min-height:1200px) and (orientation:portrait) {
    button {
        font-size: 3.6vw;
    }
}
.number {
    display: block;
    flex-basis: 6vw;
    height: 6vw;
    padding: 2vw;
    border-radius: 50%;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 700;
    line-height: 6vw;
    font-size: 3.6vw;
    box-shadow: 0 0 20px 3px black;
}

@media (orientation:portrait) {
    .number {
        flex-basis:19vw;
        height: 19vw;
        line-height: 19vw;
        font-size: 7.2vw;
    }
}

@media (min-width:1200px) {
    .number {
        flex-basis: 4vw;
        height: 4vw;
        line-height: 4vw;
        font-size: 3.2vw;
    }
}

@media (min-height:1200px) and (orientation:portrait) {
    .number {
        flex-basis:13vw;
        height: 13vw;
        line-height: 13vw;
        font-size: 6.6vw;
}
}
.panel {
    flex-basis: 50vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 5vw;
    background-image: linear-gradient(transparent, rgba(0,0,0,.2));
}

@media (orientation: portrait) {
    .panel {
        flex-basis: 65vh;
        flex-wrap: wrap;
    }
}
footer {
    flex-basis: 10vh;
    background-color: #009be4;
    background-image: linear-gradient(#009be4 70%, #0079b2);
}

h2 {
    font-size: 2.7vw;
    letter-spacing: 1px;
    text-align: center;
    line-height: 10vh;
}

.brand a {
    color: #000;
    cursor: pointer;
    text-decoration: underline;
}

.brand a:hover {
    -webkit-animation: colorChange .5s 1 linear both;
            animation: colorChange .5s 1 linear both;
}

@-webkit-keyframes colorChange {
    50% {
        color: goldenrod;
        text-shadow: 0 0 .5vw black;
    }
    100% {
        color: #000;
        text-shadow: none;
    }
}

@keyframes colorChange {
    50% {
        color: goldenrod;
        text-shadow: 0 0 .5vw black;
    }
    100% {
        color: #000;
        text-shadow: none;
    }
}

@media (orientation: portrait) {  
    footer {
        flex-basis: 5vh;
    }
     h2 {
        font-size: 4.5vw;
        line-height: 5vh;
    }
}

@media (min-width: 1200px) {
    footer {
        border-bottom-right-radius: 1vw; 
        border-bottom-left-radius: 1vw; 
    }
    h2 {
        font-size: 1.6vw;
    }
}

@media (min-height: 1200px) and (orientation: portrait) {
    footer {
        border-bottom-right-radius: 1vw; 
        border-bottom-left-radius: 1vw; 
    }
    h2 {
        font-size: 3vw;
    }
}
